import React from "react";
import MovieList from "./MovieList";
import { useSelector } from "react-redux";

const SecondaryContainer = () => {
  const movies = useSelector((store) => store?.movies);
  return (
    <div>
      <div className="bg-black">
        <div className="mt-0 md:-mt-52  pl -12 relative z-20">
          <MovieList title={"Now Playing Movies"} movies={movies?.nowPlayingMovies}></MovieList>
          <MovieList title={"Trending"} movies={movies?.popularMovies}></MovieList>
          <MovieList title={"Popular"} movies={movies?.popularMovies}></MovieList>
          <MovieList title={"Upcoming Movies"} movies={movies?.popularMovies}></MovieList>
          <MovieList title={"Horror Movies"} movies={movies?.popularMovies}></MovieList>
        </div>
      </div>
    </div>
  );
};

export default SecondaryContainer;
